import React from 'react';
import {StyleSheet, FlatList, ActivityIndicator, Text, View, Image , TouchableOpacity } from 'react-native';
import Bevitel from "./Bevitel"

export default class FetchExample extends React.Component {

  constructor(props){
    super(props);
    this.state ={
      isLoading: true,

      akttema:1,
      dataSource:[],
      dataSource2:[],


      }

      setInterval(()=> {
        this.kivalaszt(this.state.akttema)
     }, 2000);
  

  }

  componentDidMount(){
    return fetch('https://s1.siralycore.hu:8083/tema')
      .then((response) => response.json())
      .then((responseJson) => {

        this.setState({
          isLoading: false,
          dataSource: responseJson,
        }, function(){

        });

        this.setState({akttema:this.state.dataSource[0].tema_id})
        this.kivalaszt(this.state.akttema)

      })
      .catch((error) =>{
        console.error(error);
      });
  }

  kivalaszt = async(szam)=>{
    //alert(szam)
    this.setState({akttema:szam})
    let bemenet={
      bevitel1:szam
    }
    return fetch('https://s1.siralycore.hu:8083/temauzenet', {
      method: "POST",
      body: JSON.stringify(bemenet),
      headers: {"Content-type": "application/json; charset=UTF-8"}
      } )
      .then((response) => response.json())
      .then((responseJson) => {

        this.setState({
          isLoading: false,
          dataSource2: responseJson,
        }, function(){

        });
        //alert(JSON.stringify(this.state.dataSource2))
      })
      .catch((error) =>{
        console.error(error);
      });
  }


  render(){

    if(this.state.isLoading){
      return(
        <View style={{flex: 1, padding: 20}}>
          <ActivityIndicator/>
        </View>
      )
    }

    return(
      <View style={{flex: 1, paddingTop:50,backgroundColor:"#000033"}}>
{/*---------------------------------------------------------------témák*/}
       
       <View>
        <FlatList
          data={this.state.dataSource}
          renderItem={({item}) => 

          <View style={{alignItems:"center",marginTop:10}}>
            <TouchableOpacity
            style={{borderWidth:1,borderRadius:10,width:280,height:35,margin:5,backgroundColor:"grey"}}
            onPress={async ()=>this.kivalaszt(item.tema_id)}
            >
          <Text style={{textAlign:"center",fontSize:20,color:"white"}}>{item.tema_nev} </Text>
          </TouchableOpacity>
          </View>
        
        }
          keyExtractor={({tema_id}, index) => tema_id}
        />
        </View>
{/*------------------------------------------------------------Beviteli mezők*/}

<Bevitel akttema_bevitel={this.state.akttema} frissit={()=>this.kivalaszt(this.state.akttema)}   />


{/*----------------------------------------------------------üzenetek */}  
        <View style={{alignItems:'center'}}>
        <FlatList
          data={this.state.dataSource2}
          renderItem={({item}) => 
          <View style={{borderWidth:1,borderRadius:10,padding:10,width:500,marginLeft:13,paddingLeft:15,backgroundColor:"lightblue"}}>
            <Text style={{fontSize:20,padding:3,color:"white"}}>{item.uzenet_szoveg} </Text>
            <Text style={{fontStyle:"italic",fontSize:15,padding:3}}>{item.uzenet_nev} </Text>
            <Text style={{fontSize:12}}>{item.uzenet_datum} </Text>
          </View>
        }
          keyExtractor={({uzenet_id}, index) => uzenet_id}
        />
      </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  
  kekgomb: {
    alignItems: "center",
    backgroundColor: "blue",
    padding: 10,
    width:300,
    marginLeft:"auto",
    marginRight:"auto",
  }
});